.hero {
  background: #0f2027; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 500px;
  padding-top: 250px;
  color: white;
  
  
}
.hero-bar {
  background: #0f2027; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: white;
    
}

.hero-bar button{
    color:white;
  
}

.social-links{

    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: auto;
    text-align: center;
    padding: 40px;
}
.social-links i{
    
    color: white;
    font-size: 3em;
    padding: 8px;

}
